import {
    Component,
    h,
    Host,
    Prop,
    State,
    Element,
    Method,
    Event,
    EventEmitter,
} from '@stencil/core';
import { cva } from 'class-variance-authority';
import {
    computePosition,
    flip,
    shift,
    offset,
    autoUpdate,
    type Placement,
} from '@floating-ui/dom';
import {
    createMenuKeydownHandler,
    focusFirstElement,
} from '../../utils/keyboard-navigation';

/**
 * @category Actions
 * @description A versatile menu component for actions with keyboard support and positioning. Features auto-open behavior, customizable triggers, and accessible menu item management.
 */

export type Position = 'top' | 'bottom' | 'left' | 'right';
export type Align = 'start' | 'end';
export type OpenOn = 'hover' | 'click';
export type AriaRole = 'menu' | 'listbox';

const variants = cva(
    'z-menu w-full rounded-md border border-solid border-light bg-white p-4 shadow-md',
    {
        variants: {
            open: {
                true: null,
                false: 'hidden',
            },
        },
    },
);

@Component({
    tag: 'atui-menu',
    shadow: false,
})
export class AtuiMenu {
    /**
     * Menu's x offset from edge in pixels. Only applied for origin_x = 'start' | 'end'
     */
    @Prop() offset_x: number = 0;

    /**
     * Menu's y offset from edge in pixels. Only applied for origin_y = 'top' | 'bottom'
     */
    @Prop() offset_y: number = 0;

    /**
     * Position of opened menu element relative to trigger element.
     */
    @Prop() position: Position = 'bottom';

    /**
     * Alignment of opened menu element relative to trigger element.
     */
    @Prop() align: Align = 'start';

    /**
     * String representing the 'width' style of the menu element ('auto' or 'NUMpx'). Use auto when you want the menu to inherit the host's width..
     */
    @Prop() width?: string = '280px';

    /**
     * Prevent closing of menu when options are selected. Used for multi-selection controls.
     */
    @Prop() autoclose: boolean = true;

    /**
     * Event type that triggers the menu open state. Click or Hover.
     */
    @Prop() trigger: OpenOn = 'click';

    /**
     * Close the menu when the user clicks within the menu panel. Default for single selection menus.
     */
    @Prop() role: AriaRole = 'menu';

    /**
     * Prevent opening menu
     */
    @Prop() disabled: boolean = false;

    /**
     * Use portal-style positioning (fixed to viewport) instead of relative positioning - required for ag-grid cell components
     */
    @Prop() portal: boolean = false;

    @State() isOpen = false;
    triggerEl: HTMLElement;
    menuEl: HTMLElement;
    private portalContainer: HTMLElement | null = null;
    private cleanupAutoUpdate?: () => void;
    private updatePosition = async () => {
        if (this.triggerEl && this.menuEl && this.isOpen) {
            await this.updateFloatingPosition();
        }
    };

    @Element() el: HTMLElement;

    /**
     * Toggles the dropdown menu's open state.
     */
    @Method()
    async toggleMenu(): Promise<void> {
        this.atuiMenuStateChange.emit(!this.isOpen);
        this.isOpen = !this.isOpen;

        if (this.isOpen && this.portal) {
            this.moveMenuToPortal();
        } else if (!this.isOpen && this.portal) {
            this.cleanupPortalContainer();
        }

        this.updatePosition();
    }

    /**
     * Toggles the dropdown menu's open state.
     */
    @Method()
    async openMenu(): Promise<void> {
        this.isOpen = true;
        this.atuiMenuStateChange.emit(true);

        if (this.portal) {
            this.moveMenuToPortal();
        }

        await this.updatePosition();
    }

    /**
     * Toggles the dropdown menu's open state.
     */
    @Method()
    async closeMenu(): Promise<void> {
        this.isOpen = false;
        this.atuiMenuStateChange.emit(false);

        if (this.portal) {
            this.cleanupPortalContainer();
        }
    }

    /**
     * Return the current menu open state
     */
    @Method()
    async getIsOpen(): Promise<boolean> {
        return this.isOpen;
    }

    /**
     * Emits an event containing the open menu state.
     */
    @Event() atuiMenuStateChange: EventEmitter<boolean>;

    private timedOutCloser;

    componentDidLoad() {
        window.addEventListener('click', this.handleOutsideClick);
        this.setupFloatingUI();
    }

    disconnectedCallback() {
        window.removeEventListener('click', this.handleOutsideClick);
        this.cleanupFloatingUI();
        this.cleanupPortalContainer();
    }

    private mouseEnterHandler(): void {
        if (this.timedOutCloser) clearTimeout(this.timedOutCloser);
        if (!this.isOpen) {
            this.isOpen = true;
            this.atuiMenuStateChange.emit(true);
            if (this.portal) {
                this.moveMenuToPortal();
            }
            this.updatePosition();
        }
    }

    private mouseLeaveHandler(): void {
        this.isOpen = false;
        this.atuiMenuStateChange.emit(false);
        if (this.portal) {
            this.cleanupPortalContainer();
        }
    }

    private handleOutsideClick = (event: MouseEvent) => {
        if (
            this.isOpen &&
            !this.el.contains(event.target as Node) &&
            !this.portalContainer?.contains(event.target as Node)
        ) {
            this.mouseLeaveHandler();
        }
    };

    //TODO: Replace floatingUI positioning with CSS popover and anchor positioning when supported in a browsers
    private createPortalContainer(): void {
        if (!this.portalContainer) {
            this.portalContainer = document.createElement('div');
            Object.assign(this.portalContainer.style, {
                position: 'fixed',
                top: '0',
                left: '0',
                width: '100%',
                height: '100%',
                pointerEvents: 'none',
                zIndex: '9999',
            });
            this.portalContainer.setAttribute('data-atui-menu-portal', 'true');
            document.body.appendChild(this.portalContainer);
        }
    }

    private moveMenuToPortal(): void {
        if (this.portal && this.menuEl && this.isOpen) {
            this.createPortalContainer();
            this.portalContainer.innerHTML = '';
            this.portalContainer.appendChild(this.menuEl);
            this.menuEl.style.display = '';
            Object.assign(this.menuEl.style, {
                pointerEvents: 'auto',
                display: 'block',
                visibility: 'visible',
                position: 'fixed',
                width: this.width,
                zIndex: '9999',
            });

            focusFirstElement(this.menuEl);
            this.menuEl.addEventListener('keydown', this.handleMenuKeyDown);
            this.setupFloatingUI();
        }
    }

    private handleMenuKeyDown = (event: KeyboardEvent) => {
        const handler = createMenuKeydownHandler({
            container: this.menuEl,
            onEscape: () => {
                this.closeMenu();
                this.triggerEl.focus();
            },
        });
        handler(event);
    };

    private cleanupPortalContainer(): void {
        if (this.portalContainer && this.portalContainer.parentNode) {
            if (this.menuEl) {
                this.menuEl.removeEventListener(
                    'keydown',
                    this.handleMenuKeyDown,
                );
                const menuWrapper = this.el.querySelector(
                    '[data-name="menu-content-wrapper"]',
                );
                if (menuWrapper) {
                    menuWrapper.appendChild(this.menuEl);
                }
                Object.assign(this.menuEl.style, {
                    display: '',
                    visibility: '',
                    pointerEvents: '',
                    position: '',
                });
            }
            this.portalContainer.parentNode.removeChild(this.portalContainer);
            this.portalContainer = null;
        }
    }

    private setupFloatingUI() {
        if (this.cleanupAutoUpdate) {
            this.cleanupAutoUpdate();
        }

        if (this.triggerEl && this.menuEl) {
            const observer = new IntersectionObserver(
                (entries) => {
                    if (!entries[0].isIntersecting && this.isOpen) {
                        this.mouseLeaveHandler();
                    }
                },
                { threshold: 0 },
            );
            observer.observe(this.triggerEl);

            this.cleanupAutoUpdate = autoUpdate(
                this.triggerEl,
                this.menuEl,
                () => {
                    if (this.isOpen) {
                        const placement = this.getFloatingUIPlacement();
                        const strategy = this.portal ? 'fixed' : 'absolute';

                        computePosition(this.triggerEl, this.menuEl, {
                            placement,
                            strategy,
                            middleware: [
                                offset(
                                    this.position === 'bottom' ||
                                        this.position === 'top'
                                        ? this.offset_y
                                        : this.offset_x,
                                ),
                                flip({
                                    fallbackStrategy: 'bestFit',
                                    padding: 8,
                                }),
                                shift({
                                    padding: 8,
                                    mainAxis: true,
                                    crossAxis: true,
                                }),
                            ],
                        }).then(({ x, y }) => {
                            Object.assign(this.menuEl.style, {
                                position: strategy,
                                left: `${x}px`,
                                top: `${y}px`,
                                width: this.width,
                                zIndex: '9999',
                            });
                        });
                    }
                },
                {
                    ancestorScroll: true,
                    ancestorResize: true,
                    elementResize: true,
                    layoutShift: true,
                    animationFrame: true,
                },
            );

            const originalCleanup = this.cleanupAutoUpdate;
            this.cleanupAutoUpdate = () => {
                originalCleanup();
                observer.disconnect();
            };
        }
    }

    private cleanupFloatingUI() {
        if (this.cleanupAutoUpdate) {
            this.cleanupAutoUpdate();
            this.cleanupAutoUpdate = undefined;
        }
    }

    private async updateFloatingPosition(): Promise<void> {
        if (!this.triggerEl || !this.menuEl) return;
        this.setupFloatingUI();
    }

    private getFloatingUIPlacement(): Placement {
        const positionMap: Record<string, string> = {
            top: 'top',
            bottom: 'bottom',
            left: 'left',
            right: 'right',
        };

        const alignMap: Record<string, string> = {
            start: 'start',
            end: 'end',
        };

        const position = positionMap[this.position] || 'bottom';
        const align = alignMap[this.align] || 'start';

        return `${position}-${align}` as Placement;
    }

    render() {
        const classname = variants({
            open: this.isOpen && !this.disabled,
        });

        return (
            <Host>
                <div
                    class="relative"
                    onBlur={() =>
                        this.trigger === 'click' && !this.disabled
                            ? this.mouseLeaveHandler()
                            : null
                    }
                >
                    <div
                        aria-haspopup="true"
                        data-name="menu-trigger"
                        ref={(el) => (this.triggerEl = el)}
                        aria-expanded={`${this.isOpen ? 'true' : 'false'}`}
                        onMouseEnter={() =>
                            this.trigger === 'hover' && !this.disabled
                                ? this.mouseEnterHandler()
                                : null
                        }
                        onKeyDown={async (event) => {
                            switch (event.key) {
                                case 'Escape':
                                    await this.closeMenu();
                                    break;
                                case 'Enter':
                                case ' ':
                                    event.preventDefault();
                                    await this.openMenu();
                                    break;
                            }
                        }}
                        onMouseLeave={() =>
                            this.trigger === 'hover' && !this.disabled
                                ? this.mouseLeaveHandler()
                                : null
                        }
                        onClick={() =>
                            this.trigger === 'click' && !this.disabled
                                ? this.toggleMenu()
                                : null
                        }
                        class={this.disabled ? 'contents' : ''}
                    >
                        <slot name="menu-trigger" />
                    </div>

                    <div
                        role={this.role}
                        data-position={this.position}
                        data-align={this.align}
                        ref={(el) => (this.menuEl = el)}
                        aria-hidden={`${this.isOpen ? 'false' : 'true'}`}
                        onMouseEnter={() =>
                            this.trigger === 'hover' &&
                            !this.disabled &&
                            this.mouseEnterHandler()
                        }
                        onMouseLeave={() =>
                            this.trigger === 'hover' &&
                            !this.disabled &&
                            this.mouseLeaveHandler()
                        }
                        onKeyDown={(event) => {
                            if (
                                event.key === 'Enter' ||
                                event.key === ' ' ||
                                event.key === 'Escape'
                            ) {
                                if (this.autoclose) {
                                    this.mouseLeaveHandler();
                                }
                            }
                        }}
                        onClick={() =>
                            this.autoclose && this.mouseLeaveHandler()
                        }
                        class={classname}
                        data-name="menu-content-wrapper"
                    >
                        <slot name="menu-content" />
                    </div>
                </div>
            </Host>
        );
    }
}
